.product-session{margin-top:80px;padding:0 16px;position:relative}.product-session .customer-favorites{display:flex;justify-content:center;align-items:center;margin-bottom:10px}.product-session .customer-favorites .customer-content{text-align:center}.product-session .customer-favorites .customer-content label{color:#C6A26D;font-size:14px;line-height:18px}.product-session .customer-favorites .customer-content div{text-align:right}.product-session .customer-favorites .customer-content div h2{color:#C6A26D;font-size:34px;line-height:46px}.product-session .customer-favorites .customer-content div h2:first-child{margin-right:5px}.product-session .customer-favorites.desktop{margin-top:66px}.product-session .customer-favorites.tablet{display:none}.product-session .product-carousel{display:flex;justify-content:center;align-items:center;overflow:hidden}.product-session .product-carousel .product-content{width:1392px;position:relative}.product-session .product-carousel .product-content .custom-prev-product,.product-session .product-carousel .product-content .custom-next-product{position:absolute;top:50%;transform:translateY(-50%);z-index:10;cursor:pointer}.product-session .product-carousel .product-content .custom-prev-product{left:-40px}.product-session .product-carousel .product-content .custom-next-product{right:-40px}.product-session .product-carousel .product-content .product-carousel{position:relative}.product-session .product-carousel .product-content .product-card{width:260px;text-align:center;font-family:Arial,sans-serif}.product-session .product-carousel .product-content .product-card.image{background-color:#F4F0EC;padding:33px 24px;height:280px;display:flex;justify-content:center;align-items:center}.product-session .product-carousel .product-content .product-card.image img{height:232px;width:192px;object-fit:contain}.product-session .product-carousel .product-content .product-card.details{margin-top:16px}.product-session .product-carousel .product-content .product-card.details label{font-size:12px;line-height:18px;font-weight:700}.product-session .product-carousel .product-content .product-card.details p{font-size:10px;line-height:16px;color:#909294;margin-top:8px}@media screen and (max-width:1235px){.product-session::before,.product-session::after{content:"";position:absolute;top:0;width:120px;height:100%;pointer-events:none;z-index:3}.product-session::before{left:7px;background:linear-gradient(to right,#fff,rgba(255,255,255,0))}.product-session::after{right:7px;background:linear-gradient(to left,#fff,rgba(255,255,255,0))}}@media screen and (max-width:932px){.product-session{margin-top:48px}.product-session .customer-favorites.tablet{display:flex}}@media screen and (max-width:743px){.product-session{padding:0px;margin-top:40px}.product-session::before{left:-5px}.product-session::after{right:-5px}}