*,*::before,*::after{margin:0;padding:0;box-sizing:border-box;font-family:"Biryani",sans-serif;font-optical-sizing:auto;font-style:400;color:#000000}h1,h2,h3,h4,h5,h6{font-family:"Noto Serif JP",serif;font-weight:500}.layout{margin:80px auto;overflow-x:hidden}.layout .title{font-weight:500;text-align:center;font-size:48px;line-height:64px}@media screen and (max-width:932px){.layout{margin:48px auto}.layout .title{font-size:46px;line-height:56px}}@media screen and (max-width:743px){.layout{margin:40px auto}.layout .title{font-size:34px;line-height:48px}}