.register-session{display:flex;justify-content:center;margin:40px 0px;padding:0 16px}.register-session .register-content{text-align:center;max-width:1200px;display:flex;flex-direction:column;align-items:center}.register-session .register-content .title{font-size:34px;line-height:48px}.register-session .register-content .subtitle{font-size:14px;line-height:18px;margin-top:14px;margin-bottom:20px}.register-session .register-content .description{font-size:12px;line-height:16px;color:#909294;margin-top:20px;margin-bottom:32px}.register-session .register-content .button-layout{display:flex;justify-content:center;gap:48px}@media screen and (max-width:932px){.register-session{margin:48px 0px}.register-session .register-content .subtitle{margin-top:4px;margin-bottom:8px}.register-session .register-content .description{margin-top:8px;margin-bottom:8px}}@media screen and (max-width:743px){.register-session{margin:40px 0px}.register-session .register-content .subtitle{margin-top:8px;margin-bottom:16px}.register-session .register-content .description{margin-top:16px;margin-bottom:16px}}